  height: 400%;
  background: linear-gradient(170deg, rgba(238, 128, 1, 0) 0%, rgba(238, 128, 1, 0) 25%, #feb301 35%, rgba(238, 128, 1, 0) 47%, rgba(120, 24, 1, 0) 52%, #2400ff 65%, rgba(238, 128, 1, 0) 75%, rgba(238, 128, 1, 0) 100%);
  animation-name: logoGradient;
  animation-iteration-count: infinite;
  animation-duration: 8s;
}

@keyframes logoGradient {
  0% {
    transform: translate(-50%, -100%) rotate(-15deg);
  }
  18.75% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  81.25% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
